body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.favButton {
  background-color: rgba(136, 136, 136, 0.116);
  padding: 10px;
  border: none;
  width: 100%;
  cursor: pointer;
}

h1,
h2,
h5,
p,
h3 {
  text-align: center;
  margin: 10px;
}

nav {
  display: flex;
  width: 100vw;
  justify-content: flex-start;
  padding: 1rem 0;
  margin: 0 auto;
}

a,
button {
  color: #000;
  text-decoration: none;
  margin: 5px;
  cursor: pointer;
  font-weight: lighter;
  padding: 10px;
  border-radius: 5px;
}

a:hover,
button:hover {
  font-weight: bolder;
  color: rgb(136, 136, 136);
}

footer {
  display: flex;
  justify-content: center;
  text-align: center;
  padding: 2rem;
  position: relative;
  bottom: 0;
  width: 90%;
  height: 40px;
  margin-top: 2rem;
}

footer > img {
  width: 200px;
  margin: 0;
}
footer > p {
  margin: 0;
  padding-top: 0.7rem;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 300px;
  height: 100px;
  margin: 0 auto;
}

.containerCards {
  display: flex;
  flex-direction: column;
  border: 0.5px solid rgb(136, 136, 136);
  align-items: center;
  border-radius: 22px;
}

.card {
  display: flex;
  width: 200px;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  padding: 0.5rem 0.5rem 0 0.5rem;
}

.card:hover {
  border: 0.5px solid rgb(206, 217, 142);
}

.card-grid {
  display: flex;
  justify-content: space-evenly;
  gap: 1.5vw;
  width: 90vw;
  height: auto;
  flex-wrap: wrap;
  margin: 0 auto;
}

.containerFavs {
  display: flex;
  flex-direction: column;
  border: 0.5px solid rgb(136, 136, 136);
  align-items: center;
  border-radius: 22px;
  width: 250px;
  height: 400px;
  padding: 1rem;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  display: table;
  border: 1px solid #ccc;
}

td,
th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

tr:nth-child(even) {
  background-color: #dddddd;
}

